body {
  font-family: "Source Code Pro", monospace;
  font-size: 14px;
  line-height: 120%;
}

.horario {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 8px;
  padding: 10px;

  .cell {
    padding: 5px;
    border-radius: 5px;
  }

  .cell.lecture {
    border: 1px solid #ddd;
  }

  .lecture-name {
    font-weight: 600;
  }

  .cell.free {
    opacity: 0.3;
  }

  .cell.sector {
    display: flex;
    border: 1px solid #ddd;
  }

  .cell.day {
    font-size: 18px;
    text-transform: capitalize;
  }
}
